import React, { Suspense, lazy } from "react";
import { isInstaUser } from "../../../../utils";
const Author = lazy(() => import( "../../Elements/Author/Author"));
const Time = lazy(() => import( "../../Elements/Time/Time"));
const Network = lazy(() => import( "../../Elements/Network/Network"));
const Seprator = lazy(() => import( "../../Elements/Seprator/Seprator"));

const AuthorDetails = ({ Post, CardStyle, ThemeInfo, inView }) => {
  const { author, networkId } = Post
  const { ThemeId } = ThemeInfo.id
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const instaUser =  isInstaUser(author.name);

  const postAuthor = (CardStyle.author.status && !instaUser) ? true : false;
  const authorSeprator = (CardStyle.author.status && CardStyle.timeStatus && !instaUser && username != '') ? true : false;
  return <div className="tb_nc_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    <div className={`tb_nc_author`}>
      {postAuthor ? <Suspense fallback={<div className="tb_nc_author_profile"></div>}><Author author={author} network={Post.network} networkId={networkId} authorClass={'tb_nc_author_profile'} inView={inView} /> </Suspense> : ''}
      <div className="tb_nc_author_info">
        {postAuthor ? <div className="tb_nc_authorname">{author.name}</div> : ''}
        <div className="tb_nc_post_info">
          {postAuthor && username != '' ? <div className="tb_nc_username">{username}</div> : ''}
          {authorSeprator ? <Suspense fallback={<div></div>}><Seprator sepClass={`tb_nc_seprator`} /></Suspense> : ''}
          {CardStyle.timeStatus ? <Suspense fallback={<></>}>
          <Time createdAt={Post.createdAt} timeClass={`tb_nc_time tb-cTBfont-${CardStyle.author.variant}`} /></Suspense> : ''}
        </div>
      </div>
    </div>
    <div className="tb_nc_social_">
        <Suspense fallback={<div style={{minWidth:20}}></div>}>
          <Network networkClass={'tb_nc_social_ico'} networkId={networkId} iconStyle={CardStyle.icon} />
        </Suspense>
      </div>
  </div>
}
export default AuthorDetails