import React, { Component } from 'react';
import { useInView } from 'react-intersection-observer';
import { isTagShop } from '../../../actions/api';
import { themeImpressionTracking } from '../../../utils';

// Functional component wrapper
const InViewWrapper = ({ children }) => {
       const { ref, inView } = useInView({
              triggerOnce: true, // Trigger only once
              threshold: 0.01, // Trigger when 10% of the element is visible
       });
       if (isTagShop) themeImpressionTracking(ref);
       return  children({ ref, inView });
};
export default InViewWrapper