import React from "react";
import { isTagShop, isTagbox } from "../../../../actions/api";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Network from "../../Elements/Network/Network";

const Media = ({ Post, ThemeStyle, CardStyle, ThemeInfo, index, inView }) => {
  const autoPlay = (([3, 5].includes(Post.type)) && ThemeStyle.autoPlay == 1) || false
  const size = isTagbox && CardStyle.style.ratio ? CardStyle.style.ratio : false
  const networkOnTop =  !ThemeInfo.id == 59 && !isTagShop && !isTagbox ? true : false

  return <div className="tb_nc_media_wrap">
    {networkOnTop ?
      <div className="tb_nc_social_ tb_nc_social_top_">
        <Network networkClass={'tb_nc_social_ico'} networkId={Post.network.id}  network={Post.network} iconStyle={CardStyle.icon} />
      </div> : null}
      <MediaIcon Post={Post} IconClass={networkOnTop ? 'tb_nc_media_icon_te' : 'tb_nc_media_icon'} show={true}  hideVideo={autoPlay} /> 
      {autoPlay  ?
      <Video VideoClass="tb_nc_video" Post={Post} size={size} isCover={true} controls={false} autoPlay={autoPlay} muted={true} index={index} inView={inView} />
      :
      <Image ImageClass="tb_nc_image" Post={Post} size={size} hotspot={isTagShop} blurBG={false} observer={true} index={index} inView={inView} />
    }
  </div>
}
export default Media